import { getQuery } from 'api/awsQuery';
export const awsDetailsStateKey = 'awsDetails';
export const awsDetailsDefaultFilters = {
    time_scope_units: 'month',
    time_scope_value: -1,
    resolution: 'daily',
};
export const awsDetailsTabFilters = Object.assign({}, awsDetailsDefaultFilters, { limit: 3 });
export function getGroupByForTab(tab) {
    switch (tab) {
        case "accounts" /* accounts */:
            return { account: '*' };
        case "regions" /* regions */:
            return { region: '*' };
        case "services" /* services */:
            return { service: '*' };
        default:
            return {};
    }
}
export function getQueryForWidget(widget, filter = awsDetailsDefaultFilters) {
    const query = {
        filter,
        group_by: getGroupByForTab(widget.currentTab),
    };
    return getQuery(query);
}
