import { getType } from 'typesafe-actions';
import { setWidgetTab } from './ocpOnAwsDetailsActions';
import { clusterWidget } from './ocpOnAwsDetailsWidgets';
export const defaultState = {
    currentWidgets: [clusterWidget.id],
    widgets: {
        [clusterWidget.id]: clusterWidget,
    },
};
export function ocpOnAwsDetailsReducer(state = defaultState, action) {
    switch (action.type) {
        case getType(setWidgetTab):
            return Object.assign({}, state, { widgets: Object.assign({}, state.widgets, { [action.payload.id]: Object.assign({}, state.widgets[action.payload.id], { currentTab: action.payload.tab }) }) });
        default:
            return state;
    }
}
