import { getQueryForWidget, ocpOnAwsDetailsDefaultFilters, ocpOnAwsDetailsStateKey, ocpOnAwsDetailsTabFilters, } from './ocpOnAwsDetailsCommon';
export const selectOcpOnAwsDetailsState = (state) => state[ocpOnAwsDetailsStateKey];
export const selectWidgets = (state) => selectOcpOnAwsDetailsState(state).widgets;
export const selectWidget = (state, id) => selectWidgets(state)[id];
export const selectCurrentWidgets = (state) => selectOcpOnAwsDetailsState(state).currentWidgets;
export const selectWidgetQueries = (state, id) => {
    const widget = selectWidget(state, id);
    const filter = Object.assign({}, ocpOnAwsDetailsDefaultFilters, (widget.filter ? widget.filter : {}));
    const tabsFilter = Object.assign({}, ocpOnAwsDetailsTabFilters, (widget.tabsFilter ? widget.tabsFilter : {}));
    return {
        previous: getQueryForWidget(widget, Object.assign({}, filter, { time_scope_value: -2 })),
        current: getQueryForWidget(widget, filter),
        tabs: getQueryForWidget(widget, Object.assign({}, tabsFilter, { resolution: 'monthly' })),
    };
};
