import { awsDetailsDefaultFilters, awsDetailsStateKey, awsDetailsTabFilters, getQueryForWidget, } from './awsDetailsCommon';
export const selectAwsDetailsState = (state) => state[awsDetailsStateKey];
export const selectWidgets = (state) => selectAwsDetailsState(state).widgets;
export const selectWidget = (state, id) => selectWidgets(state)[id];
export const selectCurrentWidgets = (state) => selectAwsDetailsState(state).currentWidgets;
export const selectWidgetQueries = (state, id) => {
    const widget = selectWidget(state, id);
    const filter = Object.assign({}, awsDetailsDefaultFilters, (widget.filter ? widget.filter : {}));
    const tabsFilter = Object.assign({}, awsDetailsTabFilters, (widget.tabsFilter ? widget.tabsFilter : {}));
    return {
        previous: getQueryForWidget(widget, Object.assign({}, filter, { time_scope_value: -2 })),
        current: getQueryForWidget(widget, filter),
        tabs: getQueryForWidget(widget, Object.assign({}, tabsFilter, { resolution: 'monthly' })),
    };
};
