import { getQuery } from 'api/ocpOnAwsQuery';
export const ocpOnAwsDetailsStateKey = 'ocpOnAwsDetails';
export const ocpOnAwsDetailsDefaultFilters = {
    time_scope_units: 'month',
    time_scope_value: -1,
    resolution: 'daily',
};
export const ocpOnAwsDetailsTabFilters = Object.assign({}, ocpOnAwsDetailsDefaultFilters, { limit: 3 });
export function getGroupByForTab(tab) {
    switch (tab) {
        case "accounts" /* accounts */:
            return { account: '*' };
        case "projects" /* projects */:
            return { project: '*' };
        case "regions" /* regions */:
            return { region: '*' };
        case "services" /* services */:
            return { service: '*' };
        default:
            return {};
    }
}
export function getQueryForWidget(widget, filter = ocpOnAwsDetailsDefaultFilters) {
    const query = {
        filter,
        group_by: getGroupByForTab(widget.currentTab),
    };
    return getQuery(query);
}
